.panning-app {
  display: flex;
  padding: 0;
  font-family: sans-serif;
}
.panning-app .app-side {
  bottom: 0;
  padding: 0 8px;
}
.panning-app .app-content {
  flex: 1;
  margin-right: 8px;
  margin-left: 8px;
  box-shadow: 0 0 10px 1px #e9e9e9;
}
.panning-app .ant-card {
  box-shadow: 0 0 10px 1px #e9e9e9;
}
.panning-app .ant-card-head-title {
  text-align: center;
}
.panning-app .ant-row {
  margin: 16px 0;
  text-align: left;
}
