.stencil-app {
  display: flex;
  padding: 0;
  font-family: sans-serif;
}
.stencil-app .app-stencil {
  position: relative;
  width: 200px;
  border: 1px solid #f0f0f0;
}
.stencil-app .app-content {
  flex: 1;
  height: 380px;
  margin-right: 8px;
  margin-left: 8px;
  box-shadow: 0 0 10px 1px #e9e9e9;
}
