.backgournd-grid-app {
  display: flex;
  width: 100%;
  padding: 0;
  font-family: sans-serif;
}
.backgournd-grid-app .app-content {
  flex: 1;
  height: 280px;
  margin-right: 8px;
  margin-left: 8px;
  border-radius: 5px;
  box-shadow: 0 12px 5px -10px rgba(0, 0, 0, 0.1), 0 0 4px 0 rgba(0, 0, 0, 0.1);
}
