.interacting-app {
  display: flex;
  padding: 0;
  font-family: sans-serif;
}
.interacting-app .app-side {
  bottom: 0;
  height: 100%;
  padding: 0 8px;
}
.interacting-app .app-content {
  flex: 1;
  margin-top: 0;
  box-shadow: 0 0 10px 1px #e9e9e9;
}
.ant-card {
  min-height: 250px;
  box-shadow: 0 0 10px 1px #e9e9e9;
}
.ant-card-head-title {
  text-align: center;
}
.ant-row {
  margin: 16px 0;
  text-align: left;
}
.slider-value {
  display: inline-block;
  margin-left: 8px;
  padding: 3px 7px;
  color: #333;
  font-size: 12px;
  line-height: 1.25;
  background: #eee;
  border-radius: 10px;
}
