.filter-highlight-app {
  display: flex;
  padding: 0;
  padding: 16px 8px;
  font-family: sans-serif;
}
.filter-highlight-app .app-left {
  bottom: 0;
  width: 336px;
  padding: 0 8px;
}
.filter-highlight-app .app-content {
  flex: 1;
  margin-right: 8px;
  margin-left: 8px;
  box-shadow: 0 0 10px 1px #e9e9e9;
}
.filter-highlight-app .ant-card {
  box-shadow: 0 0 10px 1px #e9e9e9;
}
.filter-highlight-app .ant-card-head-title {
  text-align: center;
}
.filter-highlight-app .ant-row {
  margin: 16px 0;
  text-align: left;
}
.filter-highlight-app .slider-value {
  display: inline-block;
  margin-left: 8px;
  padding: 3px 7px;
  color: #333;
  font-size: 12px;
  line-height: 1.25;
  background: #eee;
  border-radius: 10px;
}
