.minimap-app {
  position: relative;
  padding: 0;
  font-family: sans-serif;
}
.minimap-app .app-btns {
  margin-bottom: 8px;
}
.minimap-app .app-minimap {
  position: absolute;
  bottom: 0;
  left: 620px;
  width: 200px;
  height: 150px;
}
.minimap-app .x6-widget-minimap-viewport {
  border: 2px solid #8f8f8f;
}
.minimap-app .x6-widget-minimap-viewport-zoom {
  border: 2px solid #8f8f8f;
}
